import {JsomPost} from './util.js'
// 
export function getapplication(data) {
	return JsomPost( {
		url:global.memberUrl+'/pay/message_center/',
		data:data
	})
}
// 
export function getapplicationContent(data) {
	return JsomPost( {
		url:global.memberUrl+'/pay/message_center_content/',
		data:data
	})
}
